import { EnhancedTextBlock, Statistics } from "@rp/react/rpjson/types"

export function getStatsParagraph(
  stats: Statistics,
  paragraphNumber: number,
  sentenceNumber: number,
): EnhancedTextBlock | undefined {
  if (!stats.paragraphs[paragraphNumber]) return undefined
  const p = stats.paragraphs[paragraphNumber].find(par =>
    par.sentences.find(s => s.sentenceNumber === sentenceNumber),
  )
  const content = p ?? stats.paragraphs[paragraphNumber][0]
  return content
}
